'use client';
import Slider from "react-slick";
import { Article } from "@/types";


type Props = {
  articles: Article[];
};

export default function NewsCarousel({ articles }: Props) {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 5000,
    arrows: true,
    adaptiveHeight: true
  };

  return (
    <div className="news-carousel-container">
      <Slider {...settings}>
        {articles.map((a, i) => (
          <div key={i} className="news-slide" onClick={() => a.url && window.open(a.url, "_blank")}>
            {a.url && <img src={a.urlToImage || "https://via.placeholder.com/400x200"} alt={a.title} />}
            <h3>{a.title}</h3>
            <p>{a.description}</p>
            {a.source && <small>{a.source}</small>}
          </div>
        ))}
      </Slider>
    </div>
  );
}


